@import url('https://fonts.googleapis.com/css2?family=Roboto&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html,
body {
    height: 100%;
}

#root {
    height: 100%;
    font-family: 'Roboto', sans-serif;
}

a {
    text-decoration: none;
    color: inherit;
}

a:hover {
    color: #2061aa;
}

.bold-text {
    font-weight: bold;
}

.app {
    height: 100%;
    display: flex;
    justify-content: space-between;
}

.contact-info {
    width: 30%;
    height: 100%;
    padding: 10rem 1rem;
    background-color: #2061aa;
    margin-left: 5rem;
    color: white;
}

.contact-info h1 {
    font-size: 48px;
}

.contact-info h2 {
    font-size: 28px;
    font-weight: 500;
}

.contact-container {
    margin: 5rem 0;
}

.contact-row {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    margin: 1rem 0;
}

.contact-row a,
.contact-row p {
    margin-left: .5rem;
    color: inherit;
    font-size: 20px;
    line-height: 2rem;
}

.information{
    width: 60%;
}

.nav-bar{
    display: flex;
    margin: 1rem 0;
}

.nav-bar p {
    padding: 0 1rem;
    cursor: pointer;
    border-left: 1px solid #2061aa;
}

.nav-bar p:hover {
    color: #2061aa;
}

.nav-bar p.active {
    color: #2061aa;
}

.about-me-contact{
    display: none;
}

.information-container {
    margin: 2rem 0;
    height: 90%;
    overflow-y: scroll;
}

.profile-picture{
    width: 15rem;
    height: 15rem;
    border-radius: 15rem;
    margin: 2rem auto;
    background: url("https://media.licdn.com/dms/image/C5603AQEzExIWAGwdvw/profile-displayphoto-shrink_400_400/0/1631539493330?e=1682553600&v=beta&t=X_An8yG9juHmekGEP-12E145gYJgwRBXT-SBqb9n_VE");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    border: 2px solid #2061aa;
}

.hello-text {
    color: #2061aa;
    font-size: 28px;
    font-weight: bold;
}

.welcome-text{
    font-size: 20px;
    margin: 1rem 0;
}

.section-title{
    color: #2061aa;
    font-size: 30px;
    margin: 2rem 0 1rem;
    border-bottom: 3px solid #2061aa;
    width: 75%;
}

.information-container ul .experience {
    margin: 1rem 0;
    width: 75%;
    border-bottom: 1px solid rgba(0, 0, 0, .3);
    padding-bottom: 1rem;
}

.information-container ul .experience.no-break{
    border: none;
}

.information-container ul .experience a {
    font-size: 18px;
    font-weight: bold;
}

.information-container .tasks-title {
    margin-top: 1rem;
    font-weight: bold;
}

.information-container .tasks {
    padding: 0 1.5rem;
}

.current-employment{
    width: 50%;
    margin: 2rem auto;
    text-align: center;
}

.company-picture{
    width: 100%;
    height: 15rem;
    border-radius: 15rem;
    margin: 2rem 0;
    background: url("https://en.kirirom-digital.com/wp-content/uploads/2022/04/cropped-LOGO-02-1-2048x2048.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
}

.current-employment h3{
    font-size: 24px;
}

.current-employment .quote {
    color: #777;
    margin: .5rem;
    font-family:'Times New Roman', Times, serif;
}

.feed-container {
    padding: 1rem 0;
    border-bottom: 1px solid black;
}

.image-preview {
    margin: 1rem auto;
    width: fit-content;
}

.image-preview .image{
    width: 300px;
    height: 500px;
}

.fullsize-image{
    max-width: 100%;
    max-height: 100%;
}

.iframe-container {
    margin: 1rem auto;
    width: fit-content;
}

.desktop-iframe {
    display: block;
}

.mobile-iframe {
    display: none;
}

.feed-description{
    margin: 1rem 0;
}

@media only screen and (max-width: 900px) {
    .contact-info{
        display: none;
    }

    .information {
        width: 100%;
        margin: 0 1rem;
    }

    .information-container ul {
        list-style-type: none;
    }

    .information-container ul li {
        width: 100%;
    }

    .information-container {
        overflow: visible;
    }

    .section-title{
        width: 100%;
    }

    .current-employment{
        width: 100%;
    }

    .desktop-iframe {
        display: none;
    }
    
    .mobile-iframe {
        display: block;
    }

    .image-preview .image{
        width: 150px;
        height: 250px;
    }

    .nav-bar {
        margin: 1rem auto;
    }

    .nav-bar p {
        padding: 0 .5rem;
    }

    .about-me-contact{
        display: block;
        background-color: #2061aa;
        color: white;
        padding: 1rem;
    }

    .about-me-contact h1, .about-me-contact h2{
        text-align: center;
    }

    .about-me-contact .contact-container{
        margin: 0;
    }
}